<template>
  <form class="profile">
    <div v-if="errors">
      <pre>{{ errors }}</pre>
    </div>
    <div class="field">
      <label class="label">Name:</label>
      <div class="control">
        <input class="input" type="text" v-model="user.username" placeholder="Name">
      </div>
    </div>
    <div class="field">
      <label class="label">Image:</label>
      <file-upload
        accept="image/png,image/jpeg"
        :max-size="customImageMaxSize"
        :defaultPreview="user.image"
        @size-exceeded="onSizeExceeded"
        @changed="imageChanged" />
    </div>
    <div class="field">
      <label class="label">Email:</label>
      <div class="control">
        <input class="input" type="email" v-model="user.email" placeholder="Email">
      </div>
    </div>
    <div class="field">
      <label class="label">Bio:</label>
      <div class="control">
        <textarea class="textarea" maxlength="200" v-model="user.bio" placeholder="Bio" />
      </div>
    </div>
    <div class="field">
      <label class="label">New Password:</label>
      <div class="control">
        <input class="input" type="password" v-model="user.password" placeholder="New Password">
      </div>
    </div>
    <div class="field is-grouped">
      <div class="control">
        <button @click.prevent="updateProfile" :disabled="notChanged" class="button is-link">Update</button>
      </div>
      <div class="control">
        <button @click.prevent="resetProfile" :disabled="notChanged" class="button is-link">Reset</button>
      </div>
    </div>
  </form>
</template>

<script>
import Auth from '@/api/auth'
import { mapGetters } from 'vuex'
import deepEqual from 'fast-deep-equal'
import FileUpload from '@/components/FileUpload'

export default {
  name: 'Settings',
  components: {
    FileUpload
  },
  data () {
    return {
      user: {},
      errors: null,
      customImageMaxSize: 1
    }
  },
  computed: {
    ...mapGetters({
      userProfile: 'user/userDetails'
    }),
    notChanged () {
      return deepEqual(this.userProfile, this.user)
    }
  },
  created () {
    this.updateUser(this.userProfile)
  },
  methods: {
    updateUser (value) {
      this.user = JSON.parse(JSON.stringify(value))
    },
    updateProfile () {
      const payload = {
        user: {
          email: this.user.email,
          username: this.user.username,
          password: this.user.password,
          image: this.user.image,
          bio: this.user.bio
        }
      }
      this.errors = null
      Auth.updateUser(payload)
        .catch(err => {
          this.errors = err.response.data.errors
        })
    },
    resetProfile () {
      this.errors = null
      this.updateUser(this.userProfile)
    },
    imageChanged (dataUri) {
      this.user.image = dataUri
    },
    onSizeExceeded (size) {
      alert(`Image ${size}Mb size exceeds limits of ${this.customImageMaxSize}Mb!`)
    }
  },
  watch: {
    userProfile (value) {
      this.updateUser(value)
    }
  }
}
</script>
