<template>
  <div class="file-upload">
    <div class="file-upload-actions">
      <div class="file-upload-button">
        <button
          type="button"
          class="button is-link"
          v-html="buttonInput" />
        <input
          type="file"
          @change="onChange"
          :style="fileInputStyles"
          :accept=accept>
      </div>
      <div v-if="preview" class="file-upload-button">
        <button
          type="button"
          class="button is-link"
          @click.prevent="onRemove"
          v-html="buttonRemove" />
      </div>
    </div>
    <img
      v-if="preview && !disablePreview"
      :src="preview"
      class="file-upload-image">
  </div>
</template>

<script>
export default {
  name: 'FileUpload',
  props: {
    accept: {
      type: String,
      default: 'image/png,image/gif,image/jpeg'
    },
    maxSize: {
      type: Number,
      default: 10 // megabytes
    },
    buttonInput: {
      type: String,
      default: 'Upload'
    },
    buttonRemove: {
      type: String,
      default: 'Remove'
    },
    disablePreview: {
      type: Boolean,
      default: false
    },
    defaultPreview: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      file: null,
      preview: this.defaultPreview || null
    }
  },
  computed: {
    fileInputStyles () {
      return {
        'width': '100%',
        'position': 'absolute',
        'top': 0,
        'left': 0,
        'right': 0,
        'bottom': 0,
        'opacity': 0,
        'overflow': 'hidden',
        'outline': 'none',
        'cursor': 'default'
      }
    }
  },
  watch: {
    defaultPreview (value) {
      this.preview = value
    }
  },
  methods: {
    onChange (e) {
      const files = e.target.files || e.dataTransfer.files

      if (!files.length) {
        return
      }

      const file = files[0]
      const size = file.size && (file.size / Math.pow(1000, 2))

      // check file max size
      if (size > this.maxSize) {
        this.$emit('size-exceeded', size)
        return
      }

      // update file
      this.file = file
      this.$emit('file', file)

      const reader = new FileReader()

      reader.onload = e => {
        const dataURI = e.target.result

        if (dataURI) {
          this.$emit('changed', dataURI)

          this.preview = dataURI
        }
      }

      // read blob url from file data
      reader.readAsDataURL(file)
    },
    onRemove () {
      this.file = null
      this.preview = null
      this.$emit('changed', null)
    }
  }
}
</script>

<style>
.file-upload-image {
  max-width: 30%;
  display: block;
  margin: 0 0 15px;
}

.file-upload-actions {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin: 0 0 15px;
}

.file-upload-button {
  display: inline-flex;
  position: relative;
  margin: 0 10px 0 0;
}
</style>
